.double-button {
	// max-width: 300px;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 50px;
	cursor: pointer;

	.double-button--left,
	.double-button--right {
		background-color: var(--palette-live-grey-0);
		height: 100%;
		box-sizing: border-box;
		color: var(--palette-live-grey-8);
		border: none;
		padding: 0;
		font-size: 18px;
		cursor: pointer;
	}

	.double-button--left {
		flex: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		border-top-left-radius: 50px;
		border-bottom-left-radius: 50px;
		border-right: 1px solid var(--palette-live-grey-8);
		color: var(--palette-live-grey-8);
	}

	.double-button--right {
		flex: 2;
		border-top-right-radius: 50px;
		border-bottom-right-radius: 50px;
		position: relative;
		overflow: hidden;

		&:before {
			content: "";
			position: absolute;
			height: 100%;
			width: 100%;
			background: var(--palette-live-grey-8);
			opacity: 0.3;
			left: 0;
			bottom: 0;
			transition: transform .5s;
			transform: translateX(-100%);
			clip-path: polygon(0 0, 0 100%, 0 100%, 0 0);
		}

		&.clicked:before {
			transform: translateX(100%);
			clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
		}
	}
}

.double-button--fixed {
	display: flex;
	justify-content: center;
	position: fixed;
	left: 0;
	right: 0;
	bottom: 135px;
	margin: 0 16px 0 16px;

	.double-button--left {
		background-color: var(--palette-live-grey-0);
		padding: 15px 35px 15px 35px;
		display: flex;
		border-radius: 100px;
		cursor: pointer;
		border: solid var(--palette-live-bg-1-white-old);
		border-top-width: 0;
		border-bottom-width: 0;
		border-left-width: 0;
		border-right-width: 1px;
		border-top-left-radius: 100px;
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
		border-bottom-left-radius: 100px;
		color: var(--palette-live-grey-8);
		font-size: 20px;
		font-weight: 500;

		.back-icon {
			width: 24px;
			height: 24px;
			fill: var(--palette-live-bg-1-white-old);
		}
	}

	.double-button--right {
		max-width: 307px;
		background-color: var(--palette-live-grey-0);
		padding: 15px 0 15px 0;
		display: flex;
		border-radius: 100px;
		cursor: pointer;
		border: 0;
		color: var(--palette-live-bg-1-white-old);
		font-size: 20px;
		font-weight: 500;
		border-top-left-radius: 0;
		border-top-right-radius: 100px;
		border-bottom-right-radius: 100px;
		border-bottom-left-radius: 0;
		flex: 1;
		justify-content: center;
		align-items: center;
	}
}