.stage-form {
	background-color: var(--palette-live-bg-1-white);
	// border-top-left-radius: 20px;
	// border-top-right-radius: 20px;
	// border-bottom-right-radius: 0;
	// border-bottom-left-radius: 0;
	flex: 1;
	position: relative;
	display: flex;
	flex-direction: column;

	&__wrapper {
		flex: 1;

		.stage-form__header {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 16px 16px 16px 16px;
			border: solid var(--palette-live-grey-7);
			border-top-width: 0;
			border-right-width: 0;
			border-bottom-width: 1px;
			border-left-width: 0;
			margin: 0 16px 0 16px;

			.stage-form__quantity {
				margin: 0 0 0 0;
				color: var(--palette-live-grey-0);
				font-size: var(--font-size-20);
				font-weight: var(--font-weight-medium);
			}
			
			.stage-form__stage-number-wrapper {
				display: flex;

				.stage-form__stage-number {
					margin: 0 0 0 0;
					color: var(--palette-live-grey-0);
					font-size: var(--font-size-22);
					font-weight: var(--font-weight-semi-bold);
					display: flex;
					align-items: center;
				}

				.edit-note__icon {
					width: 35px;
					height: 35px;
					fill: var(--palette-live-grey-6);
					margin: 0 0 0 20px;
				}
			}

			.arrow-down__icon {
				width: 20px;
				height: 20px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
				cursor: pointer;
			}

			.arrow-back__icon {
				width: 25px;
				height: 25px;
				fill: var(--palette-live-grey-0);
			}

			.pre-stage__header-wrapper {
				flex: 1;
				display: flex;
				flex-direction: column;
				align-items: center;

				.pre-stage__header-title {
					color: var(--palette-live-grey-0);
					font-size: var(--font-size-18);
					margin: 0 0 0 0;
					font-weight: var(--font-weight-bold);
				}

				.pre-stage__header-description {
					color: var(--palette-live-grey-0);
					font-size: var(--font-size-18);
					margin: 10px 0 0 0;
					font-weight: var(--font-weight-bold);
				}
			}
		}

		.stage-form__list-participants {
			margin: 0 16px 20px 16px;

			.stage-form__list-participants-container {
				display: flex;
				align-items: center;
				padding: 16px 16px 16px 16px;
				border: solid;
				border-top-width: 1px;
				border-bottom-width: 1px;
				border-right-width: 1px;
				border-left-width: 1px;

				&--modded {
					padding: 15px;
					border: solid var(--palette-live-grey-11);
					border-top-width: 0;
					border-bottom-width: 1px;
					border-right-width: 0;
					border-left-width: 0;
					justify-content: space-between;
				}

				.stage-form__list-participants-wrapper {
					display: flex;
				}
	
				.stage-form__list-participants-index {
					font-size: var(--font-size-18);
					margin: 0 20px 0 0;
					font-weight: var(--font-weight-regular);
					color: var(--palette-live-grey-0);
				}
	
				.stage-form__list-participants-name {
					font-size: var(--font-size-18);
					margin: 0 0 10px 0;
					font-weight: var(--font-weight-regular);
	
					&:last-child {
						margin: 0 0 0 0;
					}
				}
	
				.stage-form__list-participants-rating {
					font-size: var(--font-size-18);
					margin: 0 0 0 16px;
					font-weight: var(--font-weight-regular);
				}
			}
		}
	
		.stage-form__level {
			margin: 0 16px 0 16px;

			&-wrapper {
				display: flex;
				justify-content: space-between;
				margin: 20px 16px 20px 16px;

				.stage-form__level-content {
					display: flex;
					align-items: center;

					.stage-form__level-name {
						color: var(--palette-live-grey-0);
						font-size: var(--font-size-24);
						margin: 0 0 0 0;
						font-weight: var(--font-weight-semi-bold);
					}

					.edit-pen__icon {
						width: 24px;
						height: 24px;
						fill: var(--palette-live-grey-6);
						margin: 0 0 0 10px;
						cursor: pointer;
					}
				}

				.delete-icon {
					width: 22px;
					height: 22px;
					fill: var(--palette-live-grey-6);
					cursor: pointer;
				}
			}
		}
	
		.stage-form__button-bottom {
			margin: 20px 16px 16px 16px;
			display: flex;
			justify-content: center;
			align-items: center;

			button:first-child{
				margin-right: 5px;
			}

			button:last-child{
				margin-left: 5px;
			}
		}

		@media (max-width: 420px) {
			.stage-form__button-bottom{
				flex-direction: column;

				button:first-child{
					margin: 0 0 15px 0;
				}

				button:last-child{
					margin: 0;
				}
			}
		}

		.stage-form__button-indentation {
			margin: 0 0 20px 0;
			display: flex;
			justify-content: center;
			width: 100%;
		}

		.pre-stage__restore-wrapper {
			margin: 0 16px 20px 16px;

			.pre-stage__restore-title {
				color: var(--palette-live-grey-0);
				font-size: var(--font-size-16);
				margin: 0 0 0 0;
				font-weight: var(--font-weight-medium);
				background-color: var(--palette-live-bg-0-white);
				padding: 5px 5px 5px 5px;
				position: relative;
				top: 8px;
				left: 16px;
			}
		}
	}

	.stage-form__delete-button {
		width: 100%;
		color: var(--palette-live-grey-7);
		font-size: var(--font-size-20);
		margin: 20px 0 20px 0;
		padding: 0 0 0 0;
		border: 0;
		font-weight: 500;
		background-color: var(--palette-live-bg-1-white);
		cursor: pointer;
	}

	.stage-form__confirmation {
		color: var(--palette-live-grey-0);
		font-size: var(--font-size-15);
		margin: 0 16px 20px 16px;
		text-align: center;
	}

	.stage-form__confirmation-buttons {
		display: flex;
		justify-content: center;
		margin: 0 16px 0 16px;

		button:first-child{
			margin-right: 5px;
		}

		button:last-child{
			margin-left: 5px;
		}
	}

	.stage-form__no-group {
		display: flex;
		padding: 13px;
		border: 1.5px dashed var(--palette-live-red-1);
		border-radius: 10px;

		.stage-form__no-group--title {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 0 0;
			text-align: center;
		}
	}
}

.pre-stage__button-wrapper {
	margin: 0 16px 20px 16px;
}