.stage-group {
	border-radius: 16px;
	border: 1px solid var(--palette-live-grey-7);
	margin: 0 0 20px 0;

	&:last-child {
		margin: 0 0 0 0;
	}

	&__headers {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 16px 20px 16px 20px;
		border-bottom: 1px solid var(--palette-live-grey-7);

		.stage-group__participants {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-20);
			margin: 0 0 0 0;
			font-weight: var(--font-weight-medium);
		}

		.stage-group__group-container {
			display: flex;
			align-items: center;

			.struc-new__icon {
				width: 25px;
				height: 25px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
			}

			.round-robin__icon {
				width: 25px;
				height: 25px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
			}

			.stage-group__identify {
				color: var(--palette-live-grey-0);
				font-size: var(--font-size-18);
				margin: 0 16px 0 16px;
				font-weight: var(--font-weight-bold);
			}

			.edit-pen__icon {
				width: 30px;
				height: 24px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-6);
			}
		}

		.delete-icon {
			width: 24px;
			height: 24px;
			fill: var(--palette-live-grey-6);
			cursor: pointer;
		}

		.arrow-down__icon {
			width: 20px;
			height: 20px;
			flex-shrink: 0;
			fill: var(--palette-live-grey-0);
		}
	}

	&__headers--close {
		border-bottom: none;
	}
}

.stage-group__popup-participants-item {
	border: solid var(--palette-live-grey-9);
	border-top-width: 1px;
	border-bottom-width: 0;
	border-left-width: 0;
	border-right-width: 0;
	padding: 20px 16px;
	box-sizing: border-box;

	&--selected {
		background-color: #ebf4ff;
		cursor: pointer;
	}

	&.empty-node{
		display: flex;
		align-items: center;
	}

	&.single {
		min-height: 50px;
	}

	&.double {
		min-height: 70px;
		padding: 10px 16px;
	}

	.stage-group__popup-participants-name {
		color: var(--palette-live-grey-0);
		font-size: var(--font-size-18);
		margin: 0 0 0 0;
		font-weight: var(--font-weight-regular);
	}

	.stage-group__popup-participants-doubles {
		display: flex;
		align-items: center;

		.stage-group__popup-doubles-rating {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 16px 0 0;
			font-weight: var(--font-weight-regular);
		}

		.stage-group__popup-doubles-name {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 0 0;
			font-weight: var(--font-weight-regular);
		}

		.stage-group__popup-participants-name--first {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 10px 0;
			font-weight: var(--font-weight-regular);
		}

		.stage-group__popup-participants-name--second {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 0 0;
			font-weight: var(--font-weight-regular);
		}
	}
}