.group-player-edit {
	padding: 16px 16px 16px 16px;
	display: flex;
	justify-content: center;
	align-items: center;
	border: solid var(--palette-live-grey-7);
	border-top-width: 0;
	border-bottom-width: 1px;
	border-left-width: 0;
	border-right-width: 0;

	&__list {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		flex: 1;

		.group-player-edit__list-wrapper {
			display: flex;
			align-items: center;
			width: 100%;

			&--border {
				padding: 0 0 16px 0;
				border: solid var(--palette-live-grey-7);
				border-top-width: 0;
				border-right-width: 0;
				border-bottom-width: 1px;
				border-left-width: 0;
				margin: 0 0 16px 0;
			}

			.group-player-edit__list-container {
				display: flex;
				align-items: center;
				flex: 1;
				justify-content: center;
			}

			.arrow-down__icon {
				width: 20px;
				height: 20px;
				flex-shrink: 0;
				fill: var(--palette-live-grey-0);
			}
		}

		.struc-new__icon {
			width: 25px;
			height: 25px;
			flex-shrink: 0;
			fill: var(--palette-live-grey-0);
		}

		.round-robin__icon {
			width: 25px;
			height: 25px;
			flex-shrink: 0;
			fill: var(--palette-live-grey-0);
		}

		.group-player-edit__name {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-18);
			margin: 0 0 0 16px;
			font-weight: var(--font-weight-bold);
		}
	}
}

.group-player-edit__counter {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 16px 16px 16px 16px;

	.counter-groupe__name {
		color: var(--palette-live-grey-0);
		font-size: var(--font-size-18);
		margin: 0 16px 0 0;
		font-weight: var(--font-weight-bold);
	}

	.counter-groupe__container {
		display: flex;
		border: 1px solid var(--palette-live-grey-7);
		padding: 13px 22px 13px 22px;
		border-radius: 100px;
		align-items: center;

		.counter-groupe__button {
			padding: 0 0 0 0;
			margin: 0 0 0 0;
			border: 0;
			border-radius: 0;
			background-color: var(--palette-live-bg-0-white);
			cursor: pointer;
			display: flex;

			.remove-icon {
				width: 24px;
				height: 24px;
				fill: var(--palette-live-grey-7);
				flex-shrink: 0;
			}

			.plus-white__icon {
				width: 19px;
				height: 19px;
				fill: var(--palette-live-grey-7);
				flex-shrink: 0;
			}
		}

		.counter-groupe__text {
			margin: 0 30px 0 30px;
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-20);
			font-weight: var(--font-weight-medium);
		}
	}
}

@media (max-width: 345px) {
	.group-player-edit__counter{
		flex-direction: column;
		align-items: center;

		.counter-groupe__name{
			margin-bottom: 15px;
		}
	}
}