.no-content {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;

	&__title {
		color: var(--palette-live-grey-0);
		font-size: var(--font-size-22);
		margin: 0 0 20px 0;
		font-weight: var(--font-weight-semi-bold);
	}

	&__wrapper {
		background-color: var(--palette-live-grey-8);
		border-radius: 20px;
		padding: 35px 35px 35px 35px;
		margin: 0 16px 0 16px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		.no-content__description {
			color: var(--palette-live-grey-0);
			font-size: var(--font-size-16);
			margin: 0 0 0 0;
			font-weight: var(--font-weight-regular);
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;

			&--margin {
				margin: 0 0 35px 0;

				&:last-child {
					margin: 0 0 0 0;
				}
			}

			.structure-icon {
				width: 50px;
				height: 37px;
				fill: var(--palette-live-grey-6);
				margin: 20px 0 0 0;
			}
		}
	}
}